
.modal {
  display: none; 
  position: fixed; 
  z-index: 1; 
  padding-top: 13.8rem; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0,0.4); 
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 3rem;
  border: 3px solid #888;
  width: 40%;
  border-radius: 20px;

  h2 {
    color: red;
  }

  input {
    margin-bottom: 3rem;
  }
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 4.8rem;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
